.composition {
    position: relative;
    &__photo {
        width: 55%;
        border-radius: 0.5rem;
        box-shadow: 0px .4rem .4rem rgba(0, 0, 0, 0.3);
        position: absolute;
        z-index: 10;
        transition: all .3s;
        outline-offset: 0.5rem;
        &_back {
            top: 0;
            left: 1.5rem; }
        &_middle {
            top: 3rem;
            right: 1rem; }
        &_front {
            top: 13rem;
            left: 10rem; }
        &:hover {
            outline: 1.5rem solid $color-dark-brown;
            transform: scale(1.1);
            z-index: 20; } }
    &:hover &__photo:not(:hover) {
        transform: scale(.85); } }

